.ordering-page {

  &__wrap {
    display: flex;
  }

  &__content {
    width: 54%;
    padding-left: 11%;

    h2 {
      text-align: left;
    }
  }

  &__subtitle {
    font-size: 20px;
    letter-spacing: 2px;
    text-transform: uppercase;
    margin-bottom: 40px;
  }

  &__order-wrap {
    width: 39%;
  }

  .section-tabs {
    justify-content: flex-start;
    margin-bottom: 60px;
  }

  .section-tabs-item {
    text-decoration: none;
    font-size: 24px;
    margin-right: 40px;
    margin-left: 0;
  }
}